import React, { Component } from 'react';
import { Card, Input, Button } from 'reactstrap';
import {
    CButton,
    CCard,
    CCardBody,
    CCardGroup,
    CCol,
    CContainer,
    CForm,
    CInput,
    CInputGroup,
    CInputGroupPrepend,
    CInputGroupText,
    CRow,
    CTextarea
  } from '@coreui/react';
import CIcon from '@coreui/icons-react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';
import { Table } from 'react-bootstrap';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { requestHeaderPOSTWithAuth } from '../../Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';

const FILE_NAME = "WeightSave.js";

class WeightSave extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isLoaded: false
        };
    }

    componentDidMount() {
        document.title = "Satanlabs - weight save page";
        this.setState({
            isLoaded: true
        });
    }

    submitForm = (event) => {
        event.preventDefault();

        let reqData = {
            value: event.target.inputWeight.value,
            extraNotes: event.target.inputExtraNotes.value
        };

        if(reqData.extraNotes === null || reqData.extraNotes.length < 1) {
            reqData.extraNotes = null;
        }

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        let API_URL = 'https://backend.satanlabs.com/nexus/weight/save?from=admin-portal';
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderPOSTWithAuth(JSON.stringify(reqData), userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {reloadPage();}, 200);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    render() {
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
        <div>
            <CContainer>
                <CRow className="justify-content-center">
                <CCol md="8">
                    <CCardGroup>
                    <CCard className="p-4">
                        <CCardBody>
                        <CForm onSubmit={this.submitForm}>
                            <h1>Save weight data</h1>
                            <CInputGroup className="mb-3">
                            <CInputGroupPrepend>
                                <CInputGroupText>
                                <CIcon name="cilPencil" />
                                </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput
                                placeholder="Weight value" 
                                autoComplete="on"
                                type="number"
                                step="any"
                                id="inputWeight"
                                label="Weight value"
                                required
                            />
                            </CInputGroup>
                            <CInputGroup className="mb-4">
                            <CInputGroupPrepend>
                                <CInputGroupText>
                                <CIcon name="cilNotes" />
                                </CInputGroupText>
                            </CInputGroupPrepend>
                            <CTextarea 
                                placeholder='Some extra notes, not-mandatory though' 
                                id="inputExtraNotes" 
                                rows="3"
                            />
                            </CInputGroup>
                            <CRow>
                                <CCol xs="6">
                                    <CButton color="primary" className="px-4" type='submit'>SAVE</CButton>
                                </CCol>
                                <CCol xs="6" className="text-right">
                                    <CButton color="link" className="px-0">Reset values</CButton>
                                </CCol>
                            </CRow>
                        </CForm>
                        </CCardBody>
                    </CCard>
                    </CCardGroup>
                </CCol>
                </CRow>
            </CContainer>

            <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
            <FailureModal visible={this.state.failureModalVisible} />
        </div>);
    }
}

export default WeightSave;
